body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.banner {
  width: 100%;
  height: 200px;
  background-color: transparent;
  /* background-image: linear-gradient(to bottom right, #13547a   , #80d0c7); */
}

.banner h1 {
  height: 90%; 
  width:100%;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

.search-box {
  background: white;
  width: 100%;
  height: 70px;
  /* border-radius: 40px; */
  padding: 10px;
  box-shadow: 1px 5px 5px;
}

.search-txt {
  border: none;
  background: none;
  outline: none;
  float: left;
  padding: 0;
  color: black;
  font-size: 25px;
  line-height: 50px;
  width: 95%;
}


.search-btn {
  color: #8D91BA;
  float: right;
  width: 5%;
  height: 50px;
  /* border-radius: 50%; */
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: gray;
}

.homeBtn {
  color: white;
  font-size: 25px;
}

.homePageBtnRow {
  width: 100%;
  margin: auto;
}

.homeBtn:hover  {
  color: #E0B47D;
  text-decoration: underline;
}

.searchAndButtons {
  width: 60%;
  margin-top: 10%;
}

.login {
  color: white;
}

.login a {
  color: black;
}

.login, .login button {
  font-size: 1.2rem;
}

@media all and (min-width: 480px) {
  .login {
    padding: 60px 0;
  }

  .login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
